import React from 'react';
import { Grid, Row, Col, Panel, ListGroup, ListGroupItem, Thumbnail } from 'react-bootstrap';
import Products from './products.json'
import ButtonAction from "./ButtonAction";

export default class Detail extends React.Component {

  constructor() {
    super();

    this.state = {
      product: null
    }
  }

  componentDidMount() {
    let prod = null;
    const id = parseInt(this.props.match.params.id);

    Products.map((value, key) => {
      value.products.map((v, k) => {
        if (v.code === id) {
          prod = v;
          return 0;
        }
      });
      if(!prod) return 0;
    });

    this.setState({
      product: prod
    });
  }

  render() {

    const path_public = process.env.PUBLIC_URL + '/images/';

    const { product } = this.state;

    const box = {
      marginBottom: 60
    };

    if(!product) {
      return <div>

      </div>
    }

    return (
      <div>
        <Grid>
          <Row>
            <Col xs={12} style={box}>
              <Panel>
                <Panel.Heading
                  className="text-dark text-center"
                  key={product.code}>
                  <Thumbnail className="img-responsive" src={path_public + product.image}>
                    <strong> { product.name }</strong>
                  </Thumbnail>
                </Panel.Heading>
                <ListGroup>
                  <ListGroupItem className="text-dark">
                     <strong>Estoque:</strong> { product.amount } unidades
                  </ListGroupItem>
                  <ListGroupItem className="text-dark">
                    <strong>Custo:</strong> R$ { product.cost } reais
                  </ListGroupItem>
                  <ListGroupItem className="text-dark">
                    <strong>Preço:</strong> R$ { product.price } reais
                  </ListGroupItem>
                  <ListGroupItem className="text-dark">
                    <strong>Fornecedor:</strong> { product.supplier }
                  </ListGroupItem>
                  <ListGroupItem className="text-dark">
                    <strong>Descrição:</strong>
                    <Panel>
                      <Panel.Body>
                        { product.description }
                      </Panel.Body>
                    </Panel>
                  </ListGroupItem>
                </ListGroup>
              </Panel>
            </Col>
          </Row>
        </Grid>
        <ButtonAction icon="glyphicon-edit" action={`/products/${product.code}/edit`} key="details"/>
      </div>
    );
  }
}


