import React from 'react';
import { Grid, Row, Col } from 'react-bootstrap';

export default class NotFound extends React.Component {
  render() {
    const style = {
      textAlign: "center"
    };

    return (
      <Grid>
        <Row>
          <Col xs={12}>
            <h4 style={style}>Page not found</h4>
          </Col>
        </Row>
      </Grid>
    );
  }
}


