import React, { Component } from 'react';
import { Grid, Row, Col, ListGroup, ListGroupItem } from 'react-bootstrap';
import { Link } from 'react-router-dom';

class App extends Component {
  render() {
    return(
      <Grid>
        <Row>
          <Col xs={12}>
            <h4 className="text-center title">Dashboard</h4>

            <ListGroup>
              <ListGroupItem>
                <Link className="btn-block text-dark text-center" to="/vender">Vender</Link>
              </ListGroupItem>

              <ListGroupItem>
                <Link className="btn-block text-dark text-center" to="/caixa">Caixa</Link>
              </ListGroupItem>

              <ListGroupItem>
                <Link className="btn-block text-dark text-center" to="lunch-box-pwa/products">Meus Produtos</Link>
              </ListGroupItem>
            </ListGroup>
          </Col>
        </Row>
      </Grid>
    );
  }
}

export default App;



