import React from 'react';
import { Grid, Row, Button, Glyphicon} from 'react-bootstrap';
import { Link } from 'react-router-dom';

export default class ButtonAction extends React.Component {
  static contextTypes = {
    router: () => true,
  };

  render() {
    const style = {
      background: '#FFF',
      borderTop: '1px solid #CCC',
      width: '100%',
      height: 60,
      position: 'fixed',
      bottom: 0
    };

    const style2 = {
      position: 'relative'
    };

    return (
      <Grid fluid>
        <Row style={style}>
          <Link
            className={"glyphicon btn-circle text-dark " + this.props.icon}
            to={`${this.props.action}`}/>
        </Row>
      </Grid>
    );
  }
}


