import React from 'react';
import { Nav, Navbar, NavItem } from 'react-bootstrap';

export default class Menu extends React.Component {
  render() {
    const menu = ['link 1', 'link 2', 'link 3'];

    return (
      <Navbar.Collapse>
        <Nav>
          { menu.map((value, key) =>
            <NavItem eventKey={ key }>
              { value }
            </NavItem>
          )}
        </Nav>
      </Navbar.Collapse>
    );
  }
}


