import React from 'react';
import { Grid, Row, Col, ListGroup, ListGroupItem, Panel} from 'react-bootstrap';
import { Link } from 'react-router-dom';

import Products from "./products.json";
import ButtonAction from "./ButtonAction";

export default class List extends React.Component {
  render() {

    const box = {
      marginBottom: 60
    };

    return (
      <div>
        <Grid fluid>
          <Row>
            <h4 className="text-center title"> Meus Produtos </h4>

            <Col xs={12} style={box}>
              { Products.map((value, key) =>
                <Panel>
                  <Panel.Heading
                    className="text-dark text-center"
                    key={ key }>{ value.category }
                  </Panel.Heading>
                  <ListGroup>

                    { value.products.map((value, key) =>
                      <ListGroupItem>
                        <Link
                          className="btn-block text-dark list-group-flush"
                          to={`products/${value.code}`} >
                          { value.name }
                          <span className="badge pull-right">{ value.amount }</span> </Link>
                      </ListGroupItem>
                    )}
                  </ListGroup>
                </Panel>
              )}
            </Col>
            <ButtonAction icon="glyphicon-plus" action="products/create"/>
          </Row>
        </Grid>
      </div>
    );
  }
}


