import React from 'react';
import {
  Form,
  Grid,
  Row,
  Col,
  FormGroup,
  FormControl,
  Button,
  ControlLabel
} from 'react-bootstrap';

import Products from './products.json'

export default class Forms extends React.Component {
  static contextTypes = {
    router: () => true,
  };

  constructor() {
    super();

    this.state = {
      product: undefined
    }
  }

  componentDidMount() {
    let prod = null;
    const id = parseInt(this.props.match.params.id);

    Products.map((value, key) => {
      value.products.map((v, k) => {
        if (v.code === id) {
          prod = v;
          return 0;
        }
      });
      if(!prod) return 0;
    });

    this.setState({
      product: prod
    });
  }

  render() {

    const { product } = this.state;

    return (
      <Grid>
        <Row>
          <Col xs={12}>
            <Form horizontal>
              <FormGroup controlId="formHorizontalName">
                <Col componentClass={ControlLabel} xs={4}>
                  Nome
                </Col>
                <Col xs={12}>
                  <FormControl type="text" placeholder="Nome" value={(product) ? product.name : ''} />
                </Col>
              </FormGroup>

              <FormGroup controlId="formHorizontalCusto">
                <Col componentClass={ControlLabel} xs={4}>
                  Custo
                </Col>
                <Col xs={12}>
                  <FormControl type="text" placeholder="Custo" value={(product) ? product.cost : ''} />
                </Col>
              </FormGroup>

              <FormGroup controlId="formHorizontalPrice">
                <Col componentClass={ControlLabel} xs={4}>
                  Preço
                </Col>
                <Col xs={12}>
                  <FormControl type="text" placeholder="Preço" value={(product) ? product.price : ''} />
                </Col>
              </FormGroup>

              <FormGroup controlId="formHorizontalAmount">
                <Col componentClass={ControlLabel} xs={4}>
                  Quantidade
                </Col>
                <Col xs={12}>
                  <FormControl type="number" placeholder="Quantidade" value={(product) ? product.amount : ''} />
                </Col>
              </FormGroup>

              <FormGroup controlId="formHorizontalSupplier">
                <Col componentClass={ControlLabel} xs={4}>
                  Fornecedor
                </Col>
                <Col xs={12}>
                  <FormControl type="text" placeholder="Fornecedor" value={(product) ? product.supplier : ''}/>
                </Col>
              </FormGroup>

              <FormGroup controlId="formHorizontalFile">
                <Col componentClass={ControlLabel} xs={4}>
                  Imagem
                </Col>
                <Col xs={12}>
                  <span className="btn btn-default btn-file">
                      Arquivo <FormControl type="file" placeholder="arquivo" />
                  </span>
                </Col>
              </FormGroup>

              <FormGroup controlId="formControlsDescription">
                <Col componentClass={ControlLabel} xs={4}>
                  <ControlLabel>Descrição</ControlLabel>
                </Col>
                <Col xs={12}>
                  <FormControl rows="5"  componentClass="textarea"
                               placeholder="descrição" value={(product) ? product.description : ''}/>
                </Col>
              </FormGroup>

              <FormGroup>
                <Col xsOffset={6} xs={6}>
                  <Button
                    className="btn-default pull-right"
                    type="submit">
                    Salvar
                  </Button>
                </Col>
              </FormGroup>
            </Form>
          </Col>
        </Row>
      </Grid>
    );
  }
}


